@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
  .iconSm {
    @apply text-gradB bg-gradO transition-all cursor-pointer w-[2.5rem] h-[2.5rem] hover:drop-shadow-lg hover:bg-gradB hover:text-gradO z-10;
  }
  .iconSmInvert {
    @apply text-gradO bg-gradB transition-all cursor-pointer w-[2.5rem] h-[2.5rem] hover:drop-shadow-lg hover:bg-gradO hover:text-gradB z-10;
  }
  .iconLg {
    @apply text-gradB bg-gradO transition-all cursor-pointer w-[5rem] h-[5rem] hover:drop-shadow-lg hover:bg-gradB hover:text-gradO z-10;
  }
  .iconLgInvert {
    @apply text-gradO bg-gradB transition-all cursor-pointer w-[5rem] h-[5rem] hover:drop-shadow-lg hover:bg-gradO hover:text-gradB z-10;
  }
  .portraitRight {
    @apply bg-white max-w-[69vw] max-h-[36vh] sm:max-w-[640px] sm:max-h-[900px] absolute right-full w-screen h-screen;
  }
  .portraitLeft {
    @apply bg-white max-w-[69vw] max-h-[36vh] sm:max-w-[640px] sm:max-h-[900px] absolute left-0 ;
  }
  .portraitI {
    @apply bg-white max-w-[69vw] max-h-[36vh] sm:max-w-[640px] sm:max-h-[900px] absolute w-screen h-screen;
  }
}
